<template>
    <Menu as="div" class="relative inline-block text-left w-full ">
        <div class="">
            <MenuButton
                class="p-3 inline-flex w-full justify-center rounded-md border border-gray-300 bg-white px-4  text-sm font-medium text-gray-700 shadow-sm hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2 focus:ring-offset-gray-100">
